import React from 'react';
import { bool, func } from 'prop-types';
import { StyledMenu } from './menustyle';
import { NavLink } from "react-router-dom";

const MenuList = ({ open, setOpen }) => {
  return (
    <StyledMenu open={open} onClick={() => setOpen(!open)}>
      <NavLink className="link-menu" exact activeClassName = "active" to = "/" >HOME <span className="fc-white ex-small-font">ホーム</span></NavLink>
      <NavLink className="link-menu" activeClassName = "active" to = "/Service" >SERVICE <span className="fc-white ex-small-font">サービス内容</span></NavLink>
      <NavLink className="link-menu" activeClassName = "active" to = "/Design" >DESIGN <span className="fc-white ex-small-font">デザイン</span></NavLink>
      <NavLink className="link-menu" activeClassName = "active" to = "/Works" >WORKS <span className="fc-white ex-small-font">制作実績</span></NavLink>
      <NavLink className="link-menu" activeClassName = "active" to = "/Price" >PRICE <span className="fc-white ex-small-font">料金表</span></NavLink>
      <NavLink className="link-menu" activeClassName = "active" to = "/About" >ABOUT <span className="fc-white ex-small-font">事業所概要</span></NavLink>
      <NavLink className="link-menu" activeClassName = "active" to = "/Contact" >CONTACT <span className="fc-white ex-small-font">お問い合わせ</span></NavLink>
    </StyledMenu>
  )
}

MenuList.propTypes = {
  open: bool.isRequired,
  setOpen: func.isRequired,
}

export default MenuList;
