import React from 'react';
import emailjs from 'emailjs-com';

class Form extends React.Component {

  constructor(props) {
    super(props);
    this.state = {
      isSubmitted: false,
      name: '',
      phonetic: '',
      email: '',
      category: '',
      message: ''
    }
    this.handleClick = this.handleClick.bind(this);
  }

  handleSubmit(e){
    e.preventDefault()
    emailjs.sendForm('skkyweb', 'skkyweb-contact', e.target, 'user_hpNsxHLSCtfW8R14ScSUX')
      .then((result) => {
        alert("メッセージが送信されました。");
        this.setState({isSubmitted: true});
      }, (error) => {
        alert("メッセージを送信できませんでした。")
      });
  }

  handleClick(e) {
    e.preventDefault();
    this.setState({isSubmitted: false});
    this.resetForm();
  }

  resetForm(){
    this.setState({isSubmitted: false, name: '', phonetic: '', email: '', category: '', message: ''})
  }

  render() {
    let contactForm;
    if (this.state.isSubmitted) {
      contactForm = (
        <div>
          <p className="middle-font">送信が完了しました。</p>
          <p className="middle-font">入力していただいたアドレス宛に1~2日以内にご連絡いたします。</p>
          <li className="mtb s-list-caution small-font fc-red">送信後に確認メールが届かない場合、間違ったメールアドレスを入力された可能性があります。お手数ですが内容を確認して再度送信してください。</li>
          <div className="button">
            <button onClick={this.handleClick} className="button-submit">戻る</button>
          </div>
        </div>
      );
    } else {
      contactForm = (
          <div className='fade-in'>
            <form id="contact-form" onSubmit={this.handleSubmit.bind(this)} method="POST">
              <table className="table-contact">
                <tbody>
                  <tr>
                    <th className="th-contact">
                      <label htmlFor="form-name" className="fw-500">お名前 <span className="fc-red">*</span></label>
                    </th>
                    <td className="td-contact">
                      <input id="form-name" className="input" type="text" name="name" placeholder="山田太郎" required value={this.state.name} onChange={this.onNameChange.bind(this)} ></input>
                    </td>
                  </tr>
                  <tr>
                    <th className="th-contact">
                      <label htmlFor="form-phonetic" className="fw-500">フリガナ <span className="fc-red">*</span></label>
                    </th>
                    <td className="td-contact">
                      <input id="form-phonetic" className="input" type="text" name="phonetic" placeholder="ヤマダタロウ" required value={this.state.phonetic} onChange={this.onPhoneticChange.bind(this)} ></input>
                    </td>
                  </tr>
                  <tr>
                    <th className="th-contact">
                      <label htmlFor="form-email" className="fw-500">メールアドレス <span className="fc-red">*</span></label>
                    </th>
                    <td className="td-contact">
                      <input id="form-email" className="input" type="email" name="email" placeholder="skky-web@email.com" required value={this.state.email} onChange={this.onEmailChange.bind(this)} ></input>
                    </td>
                  </tr>
                  <tr>
                    <th className="th-contact"><label className="fw-500">区分 <span className="fc-red">*</span></label></th>
                    <td className="td-contact">
                      <span className="break">
                        <label htmlFor="website"><input id="website" type="radio" name="category" required value="Webサイト" onChange={this.onCategoryChange.bind(this)} ></input>Webサイト</label>
                      </span>
                      <span className="break">
                        <label htmlFor="translation"><input id="translation" type="radio" name="category" value="翻訳" onChange={this.onCategoryChange.bind(this)} ></input>翻訳</label>
                      </span>
                      <span className="break">
                        <label htmlFor="other"><input id="other" type="radio" name="category" value="その他" onChange={this.onCategoryChange.bind(this)} ></input>その他</label>
                      </span>
                    </td>
                  </tr>
                  <tr>
                    <th className="th-contact bb-wide">
                      <label htmlFor="form_textarea" className="fw-500">お問い合わせ内容</label>
                    </th>
                    <td className="td-contact bb" colSpan="2">
                      <textarea id="form_textarea" className="textarea" name="message" rows="10" value={this.state.message} onChange={this.onMessageChange.bind(this)} />
                    </td>
                  </tr>
                </tbody>
              </table>

              <div className="button">
                <button type="submit" className="button-submit">送信</button>
              </div>

            </form>

           <p className="middle-font mb">通常1~2日以内にご連絡いたします。</p>
           <li className="s-list-caution small-font fc-red">送信後に確認メールが届かない場合、間違ったメールアドレスを入力された可能性があります。お手数ですが内容を確認して再度送信してください。</li>
         </div>
      );
    }

    return (
      <div className='contact-form'>
        {contactForm}
      </div>
    );
  }

  onNameChange(event) {
	  this.setState({name: event.target.value})
  }
  onPhoneticChange(event) {
	  this.setState({phonetic: event.target.value})
  }
  onCategoryChange(event) {
	  this.setState({category: event.target.value})
  }
  onEmailChange(event) {
	  this.setState({email: event.target.value})
  }
  onMessageChange(event) {
	  this.setState({message: event.target.value})
  }
}

export default Form;
