import React, { useState, useRef } from "react";
import { NavLink } from "react-router-dom";
import { Container, Row, Col, setConfiguration } from 'react-grid-system';
import { animated, useChain, useSpring, useTransition } from "react-spring";
import styled, { ThemeProvider } from "styled-components";

import { Box } from "./ui/box";

import { Typography } from "./ui/typography";
import { Flex } from "./ui/flex";
import { theme } from "./theme";
import data from "./data";

import HomeDay from "../../images/home-day.jpg"
import Service from "../../images/service.png"
import Design from "../../images/design.png"
import Works from "../../images/works.png"
import Price from "../../images/price.png"
import About from "../../images/about.png"
import Mail from "../../images/mail.png"

const AnimatedFlex = styled(animated(Flex))``;
AnimatedFlex.defaultProps = {};

const AnimatedBanner = styled(animated(Flex))`
  cursor: pointer;
  `;
  AnimatedBanner.defaultProps = {
  backgroundSize: "cover",
  backgroundPosition: "center center",
  backgroundRepeat: "no-repeat",
  willChange: "transform, opacity"
};

const Title = styled(animated(Typography))``;
Title.defaultProps = {
  fontSize: [5, 6, 7, 7],
  lineHeight: "2rem",
  bg: "rgba(255, 255, 255, 0.7)",
  mx: 2,
  p: [2, null, 4],
  textAlign: "center",
  fontWeight: 2,
  overflow: "hidden"
};

const AnimatedContainer = styled(animated(Box))``;
AnimatedContainer.defaultProps = {
  willChange: "width, height"
};

const AnimatedItem = styled(animated(Box))``;
AnimatedItem.defaultProps = {
  width: "100%",
  height: 200,
  background: "white",
  backgroundSize: "cover",
  backgroundPosition: "center center",
  backgroundRepeat: "no-repeat",
  willChange: "transform, opacity"
};

function Menu() {
  const [clicked, setClicked] = useState(false);

  //https://konstantinlebedev.com/animate-units-react-spring/
  const vhToPixel = value => `${(window.innerHeight * value) / 100}px`;
  // const vwToPixel = value => `${(window.innerWidth * value) / 100}px`;
  const animatedBannerRef = useRef();
  const animatedBannerProps = useSpring({
    ref: animatedBannerRef,
    height: clicked ? "100px" : vhToPixel(100),
    opacity: clicked ? 0.6 : 1,
    from: {
      height: vhToPixel(100),
      opacity: 1
    }
  });

  const titleRef = useRef();
  const titleProps = useSpring({
    ref: titleRef,
    opacity: clicked ? 0 : 1,
    transform: clicked ? "translateY(-100%)" : "translateY(0%)",
    transformOrigin: "0 0",
    from: {
      opacity: 0,
      transform: "translateY(-100%)"
    }
  });

  const animatedContainerProps = useSpring({
    padding: clicked ? 32 : 0,
    from: {
      padding: 0
    }
  });

  const animatedItemsRef = useRef();
  const transitions = useTransition(clicked ? data : [], item => item, {
  // set trail and config values to 0 when clicked === false to prevent unfinished animation
  ref: animatedItemsRef,
  unique: true,
  // trail: 500 / data.length,
  trail: clicked ? 500 / data.length : 0,
  transformOrigin: "100% 100%",
  from: {
    opacity: 0,
    transform: "scale(0)"
  },
  enter: {
    opacity: 1,
    transform: "scale(1)"
  },
  leave: {
    opacity: 0,
    transform: "scale(0)"
  },
  config: { duration: clicked ? 500 : 0 }
});

  setConfiguration({ breakpoints: [300, 600, 900, 1200] });

  useChain(
    clicked
      ? [titleRef, animatedItemsRef, animatedBannerRef]
      : [animatedItemsRef, titleRef, animatedBannerRef],
    [0, 0.5]
  );

  return (
    <ThemeProvider theme={theme}>
      <Box bg="bg500" minHeight="100vh" onClick={() => setClicked(!clicked)}>
        <AnimatedBanner
          style={animatedBannerProps}
          background={`url(${HomeDay})`}
          alignItems="center"
          justifyContent="center"
        >
          <Flex flexDirection="column" justifyContent="center">
            <Title style={titleProps}>
              <Typography className="mb-home" fontSize={5} pt={1} lineHeight={2}>
                ~格安ホームページ作成~
              </Typography>
                あなたの独立開業を応援します!!
              <Typography className="left" fontSize={2} opacity={0.7} pt={3} lineHeight={1}>
              新しく事業を始めるからホームページを持ちたい！でも作り方がわからない…誰かに作ってもらおうにも高いからちょっと…と諦めていたそこのあなた！SKKY webは、そんなあなたをサポートします！もちろんすでに事業を開始している方も大歓迎！妥協はさせません！お客様が納得いくまでトコトン付き合います！まずはお気軽にご相談ください。
              </Typography>
              <Typography fontSize={2} opacity={0.7} pt={1} lineHeight={2}>
                → <span className="link-home">More Detail</span>
              </Typography>
            </Title>
          </Flex>
        </AnimatedBanner>
        <Container>
          <AnimatedContainer style={animatedContainerProps}>
          {transitions.map(({ props }) => (
            <Row justify="center">
              <Col sm={12} md={6} lg={4}>
                <div className="menu-box link-menu">
                  <NavLink className="stroke" activeClassName = "active" to = "/Service" ><img className="pic-home" src={Service} alt="At It"></img></NavLink>
                  <NavLink className="stroke" activeClassName = "active" to = "/Service" >SERVICE</NavLink>
                  <p className="text-home small-font">サービス内容</p>
                </div>
              </Col>
              <Col sm={12} md={6} lg={4}>
                <div className="menu-box link-menu">
                  <NavLink className="stroke" activeClassName = "active" to = "/Design" ><img className="pic-home" src={Design} alt="At It"></img></NavLink>
                  <NavLink className="stroke" activeClassName = "active" to = "/Design" >DESIGN</NavLink>
                  <p className="text-home small-font">デザイン</p>
                </div>
              </Col>
              <Col sm={12} md={6} lg={4}>
                <div className="menu-box link-menu">
                  <NavLink className="stroke" activeClassName = "active" to = "/Works" ><img className="pic-home" src={Works} alt="At It"></img></NavLink>
                  <NavLink className="stroke" activeClassName = "active" to = "/Works" >WORKS</NavLink>
                  <p className="text-home small-font">制作実績</p>
                </div>
              </Col>
              <Col sm={12} md={6} lg={4}>
                <div className="menu-box link-menu">
                  <NavLink className="stroke" activeClassName = "active" to = "/Price" ><img className="pic-home" src={Price} alt="At It"></img></NavLink>
                  <NavLink className="stroke" activeClassName = "active" to = "/Price" >PRICE</NavLink>
                  <p className="text-home small-font">料金表</p>
                </div>
              </Col>
              <Col sm={12} md={6} lg={4}>
                <div className="menu-box link-menu">
                  <NavLink className="stroke" activeClassName = "active" to = "/About" ><img className="pic-home" src={About} alt="At It"></img></NavLink>
                  <NavLink className="stroke" activeClassName = "active" to = "/About" >ABOUT</NavLink>
                  <p className="text-home small-font">事業所概要</p>
                </div>
              </Col>
              <Col sm={12} md={6} lg={4}>
                <div className="menu-box link-menu">
                  <NavLink className="stroke" activeClassName = "active" to = "/Contact" ><img className="pic-home" src={Mail} alt="At It"></img></NavLink>
                  <NavLink className="stroke" activeClassName = "active" to = "/Contact" >CONTACT</NavLink>
                  <p className="text-home small-font">お問い合わせ</p>
                </div>
              </Col>
            </Row>
            ))}

          </AnimatedContainer>
        </Container>
      </Box>
    </ThemeProvider>
  );
}

export default Menu;
